<template>
  <div class="flex flex-col justify-center items-center gap-4">
    <img
      class="w-56 h-58"
      src="https://wwbm.com/images/logo.webp"
      alt="Millionaire logo"
    >
    <h1 class="font-bold text-xl text-primary text-center">
      Welcome to the Game
    </h1>
    <p class="text-center text-primary font-semibold">
      In this game, you will face very hard questions. Good luck
    </p>
    <BaseButton v-if="user" link="/quiz" :handleButton="resetStorage">
      Start game
    </BaseButton>
    <BaseButton v-if="!user" link="/login">
      Login
    </BaseButton>
    <BaseButton v-else :handleButton="logout">
      Logout
    </BaseButton>
    <BaseButton
      link="/leaderboard"
    >
      Leaderboard
    </BaseButton>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed } from 'vue'
import { localStorageService } from '@/services/localStorageService'
import { useStore } from '@/store'

export default defineComponent({
  name: 'WelcomeQuiz',
  setup () {
    const store = useStore()
    const user = computed(() => store.state.userLoggedIn)
    function resetStorage () {
      localStorageService.clear()
    }
    function logout () {
      store.dispatch('logout')
    }
    return {
      resetStorage,
      user,
      logout
    }
  }
})
</script>
